<template>
  <div class="hello">
    <ul class="videolist-wrap">

      <li v-for="v in videos" :key="v.id" @click="handleitemclick(v)"><img :src="v.iaddr" :hls="v.vaddr"/><span>{{v.id}}</span></li>

    </ul>
    <div >
      <span class="navbtn" @click="goprev">Prev</span><span class="pagenum">{{currentpage}}</span><span class="navbtn" @click="gonext">Next</span>
    </div>
  </div>
  <div v-if="showdetail" class="detail-wrap">
    <h4 class="detailtitle">{{detailtitle}}</h4>
    <VideoPlayer :options="playoption"></VideoPlayer>
    <div class="closebtn" @click="closedetail">Close</div>
  </div>
</template>

<script>
import VideoPlayer from '@/components/videoplayer.vue';
import 'video.js/dist/video-js.css';
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  components: {
		VideoPlayer
	},
  data(){
    return {
      detailtitle:"",
      showdetail:false,
      totalvideos:85885,
      player:null,
      currentpage:1,
      playoption:{},
      imgbaseurl:"https://hp.cloudfront.xyz/tmb/",
      videobaseurl:"https://hp.cloudfront.xyz/hls/mp4/"
    }
  },
  computed: {
    videos(){
      var tmpvideos=[];

      for(var i=1;i<=20;i++){
        var tmpid=this.totalvideos-(this.currentpage-1)*20-i;
        var tmpvideo={
          id:tmpid,
          iaddr:this.imgbaseurl+tmpid+`/preview.jpg`,
          vaddr:this.videobaseurl+tmpid+`.mp4/index.m3u8`
        };
        tmpvideos.push(tmpvideo);
      }
      return tmpvideos;
    }
  },
  methods:{
    goprev(){
      this.currentpage=this.currentpage-1;
    },
    gonext(){
      this.currentpage=this.currentpage+1;
    },
    handleitemclick(v){
      this.playoption = {
       autoplay:false,
       controls:true,
        width:document.body.clientWidth+"px",
        height:"300px",
        poster:v.iaddr,
        sources:[{
          src:v.vaddr,
          type: "application/x-mpegURL"}]};
          this.detailtitle=v.id;
      this.showdetail=true;
    },
    closedetail(){
      this.showdetail=false;
    }
  }
}
</script>

